.switch-btn-wrapper {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
    left: 50%;
}

.switch-btn-wrapper input {
    opacity: 0;
    width: 0;
    height: 0;
}

.switch-btn-slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 34px;
}

.switch-btn-slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
}

.switch-btn:checked + .switch-btn-slider {
    background-color: #2196F3;
}

.switch-btn:focus + .switch-btn-slider {
    box-shadow: 0 0 1px #2196F3;
}

.switch-btn:checked + .switch-btn-slider:before {
    transform: translateX(26px);
}

.switch-btn-label {
    position: absolute;
    right: 100%;
    margin-right: 50px;
    font-family: Arial, Helvetica, sans-serif;
}

